import React, { useRef } from 'react'
import './Sort.scss'
import { clientEvents } from '../../events'

export const Sort = props => {

  const selectRef = useRef('name-up')
  
  const sortObj = eo => {
    const obj = {
      sort: eo.target.value
    }
    clientEvents.emit("setSortObj", obj)
  }
	

  return (
    <div className='Sort'>
      <select onChange={sortObj} className='Sort' ref={selectRef}>
            <option value="name-up">  , A-</option>
            <option value="name-down">  , -</option>
            <option value="price-up"> , </option>
            <option value="price-down"> , </option>
      </select>
    </div>
  )
}
